import React, { Component } from 'react';
import './App.css';
import StartScreen from './screen/StartScreen';
import GameScreen from './screen/GameScreen';
import EndScreen from './screen/EndScreen';

class App extends Component {

    state = {
        screen:StartScreen,
        data : {}
    };

    constructor(props){
        super(props);
    }

    changeState(screen,data){
        console.log('data ' + JSON.stringify(data));
        switch(screen){
            case "StartScreen":
                this.state.screen = StartScreen;
                this.state.data = data;
                break;
            case "EndScreen":
                this.state.screen = EndScreen;
                this.state.data = data;
                break;
            case "GameScreen":
                this.state.screen = GameScreen;
                this.state.data = data;
                break;
        }
        this.setState(this.state);
    }

    render() {
        return (
        <center>
            < this.state.screen handleClick={this.changeState.bind(this)} data={this.state.data} />
        </center>
        );
    }
}

export default App;