import React, { Component } from 'react';

export default class Cell extends Component {

    constructor(props) {
        super(props);
    
        this.state = {
          clientRect: {},
        };
      }
    //s = {width:'4%', height:'4vh'};
    ref = node => node && this.setState({clientRect: node.getBoundingClientRect()});

    getValue(){

        

        //{"x":8,"y":8,"width":50,"height":50,"top":8,"right":58,"bottom":58,"left":8}
       // const shape = "ðŸš©";
        //<svg><rect x={0} y={0}  width={this.state.clientRect.width} height={this.state.clientRect.height} fill="olive" /></svg>;
        //<svg><circle cx={this.state.clientRect.width/2} cy={this.state.clientRect.height/2} r={this.state.clientRect.width/2} fill="red" /></svg>;
        //<svg><rect x={0} y={0}  width={this.state.clientRect.width} height={this.state.clientRect.height} fill="olive" /></svg>;
        //<svg><rect x={0} y={0}  width={25} height={25} fill="red" /></svg>;
        //<svg><circle cx={10} cy={10} r={10} fill="red" /></svg>;
        if (!this.props.value.isRevealed){
            return this.props.value.isFlagged ? 'ðŸš©' : null;//Flag
        }
        if (this.props.value.isMine) {
            return 'ðŸ’£';//Bomb
            //"ðŸš©"
        }
        if(this.props.value.neighbour === 0 ){
            return null;
        }
        return this.props.value.neighbour;
    }
    
    getCellWH(){
        let s = {width:'4%', height:'4vh'};
        console.log(this.props.screenWidth + 'this.props.screenWidth');
        if(this.props.screenWidth > 700){
            if(this.props.value.width === 8){
                this.s= {width:'12%', height:'10vh'};
            } else if(this.props.value.width === 12){
                this.s= {width:'8%', height:'7vh'};
            } else if(this.props.value.width === 16){
                this.s= {width:'6%', height:'5vh'};
            } 
            this.s['font-size'] = '24pt';
            this.s['padding-top'] = '2%';
        } else {
            if(this.props.value.width === 8){
                this.s= {width:'11%', height:'9vh'};
            } else if(this.props.value.width === 12){
                this.s= {width:'7%', height:'6vh'};
            } else if(this.props.value.width === 16){
                this.s= {width:'5%', height:'4vh'};
            } 
            this.s['font-size'] = '16pt';
            this.s['padding-top'] = '2%';
        }
        return this.s;
    }

    render(){
        let className = "cell" + (this.props.value.isRevealed ? "" : " hidden") + (this.props.value.isMine ? " is-mine" : "") + (this.props.value.isFlagged ? " is-flag" : "");
        //className = "cell is-mine";
        //const w='3%';//100/this.props.width+'%';
       // const h='3vh';//100/this.props.height+"vh";
        const s = this.getCellWH();
        //{width:w, height:h};
        return (
            <div style={s} ref="cell" onClick={this.props.onClick} className={className} onContextMenu={this.props.cMenu} ref={this.ref}>
                {this.getValue()}
            </div>
        );
    }
}
