import React, { Component } from 'react';
export default class EndScreen extends Component{

    constructor(props){
        super(props);
    }

    handleGameReStart = () => {
        this.props.handleClick('StartScreen')
    }
    playNextGame = () => {
        this.props.handleClick('GameScreen')
    }
   
    render(){
        const msg = this.props.data.wongame ? 'Congratulations You win' : 'OOOps you loos';
        return (
           
            <div className="gameEnd">
            <div className="game-info">
                <div className="instructions">
                    <h4>{msg}</h4>
                    <p>Click on Play Next to try Again.</p>
                    <p>Or Restart and select the game level and continue again.</p>
                    <p>This game will not add or deduce any score to your final credit.</p>
                </div>
                <br/><br/><br/><br/>
                <button className='screen-button' onClick={this.playNextGame}>Play Next</button>
                <button className='screen-button' onClick={this.handleGameReStart}>Restart</button>
            </div>
            </div>
        );
    }

}