import React, { Component } from 'react';
import GameBoard from '../component/gameBoard';
//import PathFinder from '../component/PathFinder';

export default class GameScreen extends Component{
    constructor(props){
        super(props);
    }

    state = {
        height: 8,
        width: 8,
        mines: 10,
        wongame: false,
    };

   wongame = (data) => {
        this.state.wongame = data;
   }

    handleGameEnd = () => {
        this.props.handleClick('EndScreen',this.state);
    }

    componentWillReceiveProps(nextProps) {
        console.log('data from parane' + JSON.stringify(nextProps))
        if (JSON.stringify(this.props) !== JSON.stringify(nextProps)) {
            this.setState({
                height: nextProps.height,
                width: nextProps.width,
                mines: nextProps.mines,
            });
        }
    }

    render(){
        const { height, width, mines } = (this.props.data) ? this.props.data : this.state;
        console.log('data from parent is ' + JSON.stringify(this.props.data))
        return (
            <div className="game">
            <div className="game-info">
               <GameBoard height={height} width={width} mines={mines} gameWin={this.wongame.bind(this)}/>
               <br/><br/>
                <button className='screen-button'  onClick={this.handleGameEnd}>End</button>
            </div>
            </div>
        );
    }
}