import React, { Component } from 'react';

export default class StartScreen extends Component{
    constructor(props){
        super(props);
    }

   state = {
        height: 8,
        width: 8,
        mines: 10,
    };

    handleGameStart = () => {
        let difficulty = document.querySelector("#level_select");
        if (difficulty.value === "beginner") {
            /*this.setState({
                height: 8,
                width: 8,
                mines: 10,
            });*/
            this.state = {
                height: 8,
                width: 8,
                mines: 10,
            }
        }
        if (difficulty.value === "intermediate") {
            /*this.setState({
                height: 12,
                width: 12,
                mines: 20,
            });*/
            this.state = {
                height: 12,
                width: 12,
                mines: 20,
            };
        }
        if (difficulty.value === "expert") {
            /*this.setState({
                height: 16,
                width: 16,
                mines: 40,
            });*/
            this.state = {
                height: 16,
                width: 16,
                mines: 40,
            }
        }
        console.log('selected level ' +difficulty.value + " state data as " + JSON.stringify(this.state)) ;
        this.props.handleClick('GameScreen',this.state);
    }

    render(){
        return (
            <div className="gameStart">
            <div className="game-info">
                <div className="instructions">
                    <h4>Rules</h4>
                    <p>You are presented with a board of squares. Some squares contain mines (bombs), others don't.</p> 
                    <p>If you click on a square containing a bomb, you lose.</p>
                    <p>If you manage to click all the squares (without clicking on any bombs) or flag all the mines, you win.</p>
                    <p>Clicking a square which doesn't have a bomb reveals the number of neighbouring squares containing bombs.</p>
                    <p>Use this information plus some guess work to avoid the bombs.</p>
                    <p>To open a square, point at the square and click on it.</p> 
                    <p>To mark a square you think is a bomb, point and right-click.</p>
                    <p>Its free game and hence, no scoring available for this game.</p>
                </div>
               <h4>Select a level and click "start"</h4>
                <span className="info">Level:
                    <select id="level_select">
                        <option value="beginner"> Beginner </option>
                        <option value="intermediate"> Intermediate </option>
                        <option value="expert"> Expert </option>
                    </select>
                </span>
                <br/><br/><br/><br/>
                <button className='screen-button' onClick={this.handleGameStart}>Start</button>
            </div>
            </div>
        );
    }

}